.dock-outer {
    margin: 0 0.5rem;
    display: flex;
    max-width: 100%;
    align-items: center;
  }
  
  .dock-panel {
    position: fixed;
    bottom: 0.5rem;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: flex-end;
    width: fit-content;
    gap: 1rem;
    border-radius: 1rem;
    background-color: #060010;
    border: 1px solid #222;
    padding: 0 0.5rem 0.5rem;
  }
  
  .dock-item {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    background-color: #060010;
    border: 1px solid #222;
    box-shadow:
      0 4px 6px -1px rgba(0, 0, 0, 0.1),
      0 2px 4px -1px rgba(0, 0, 0, 0.06);
    cursor: pointer;
    outline: none;
  }
  
  .dock-icon {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .dock-label {
    position: absolute;
    top: -1.5rem;
    left: 50%;
    width: fit-content;
    white-space: pre;
    border-radius: 0.375rem;
    border: 1px solid #222;
    background-color: #060010;
    padding: 0.125rem 0.5rem;
    font-size: 0.75rem;
    color: #fff;
    transform: translateX(-50%);
  }
  